import React from 'react'

class AnimatedNumber extends React.Component {

    state = {
        numberInitial: 0,
        numberFinal: 0,
        intervalId: 0,
        started: false
    }

    componentDidMount() {
        console.log('NUMBER FINAL: ', this.props.number)
        const intervalId = setInterval(() => {
            this.increment()
        }, 200)
        this.setState({ ...this.state, numberFinal: this.props.number, intervalId: intervalId, started: true })
    }

    increment = () => {
        if (this.state.started) {
            const { numberInitial, numberFinal } = this.state

            console.log('NumberInitial', numberInitial)

            const newNumber = numberInitial + 5

            console.log('new number: ', newNumber)
            console.log('compare numbers: ', newNumber >= numberFinal)
            if (newNumber >= numberFinal) {
                clearInterval(this.state.intervalId)
                this.setState({ ...this.state, started: false, numberInitial: numberFinal })
            } else {
                this.setState({ ...this.state, started: false, numberInitial: newNumber })
            }
        }
    }

    render() {
        return this.state.numberInitial
    }
}

export default AnimatedNumber